.leftBar {
flex: 2;
position: sticky;
top: 70px;
height: calc(100vh - 70px);
overflow: scroll;

&::-webkit-scrollbar {
  display: none;
}

  .container {
    padding: 20px;
  hr {
    margin: 20px 0;
    border: none;
    height: 0.5px;
    background-color: lightgrey;
  }
    .menu {
      display: flex;
      flex-direction: column;
      gap: 20px;
      
     span {
      font-size: 12px;
     }

      .user {
        display: flex;
        align-items: center;
        gap: 10px;

          img {
            width: 25px;
            height: 25px;
            border-radius: 50%;
            object-fit: cover;
          }
          span {
            font-size: 14px;
          }

      }

      .item {
        display: flex;
        align-items: center;
        gap: 10px;

        img {
          width: 20px;
          height: 20px;

        }
        span {
          font-size: 14px;
        }
      }
    }
  }
}